<template>
  <div>
 
    <el-form :model="prjBudget" :rules="budgetRules" ref="prjBudgetForm"
         style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
           <!-- el-row>
            <el-col :span="10">
              <el-form-item label="项目名称:">
              {{prjBudget.prjName}}
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="项目编号:">
                {{prjBudget.prjNo}}
              </el-form-item>
            </el-col>
          </el-row -->

          <el-row>
            <el-col :span="8">
              <el-form-item label="项目平均人月费用:" prop="prjManCostAvg">
                <el-input size="mini" style="width: 130px"  v-model = "prjBudget.prjManCostAvg" />
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="项目合同收入:" prop="contractAmount">
                <el-input size="mini" style="width: 130px"  v-model = "prjBudget.contractAmount" />
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="当年收入:" prop="curYearAmount">
                <el-input size="mini"  style="width: 130px" v-model = "prjBudget.curYearAmount" />
              </el-form-item>
            </el-col>


          </el-row>

          <div  v-for="budget in prjBudget.budgets" :key=budget.year>
            <template>
              <el-row>
                <el-col :span="20"> 
                  <h3>{{budget.year}}年预算(单位：人月)</h3>
                </el-col>
              </el-row>
              
          <el-row>
            <el-col v-for = "item in budget.budgets" :key=item.month :span=5 >
              <div style="margin-bottom:10px; margin-right:5px;">
                  <span class="col-label">{{item.month}}月:</span> 
                  <el-input class="col-input" v-model="item.manMonth" size="mini"></el-input>
              </div>
              </el-col>
          </el-row>

            </template>
          </div>

           <div  class="el-dialog__footer" style="margin: 2px">
              <el-button size="mini" type="primary" @click="savePrjBudget('prjBudgetForm')">提 交</el-button>
          </div>
      </div>
    </el-form>

  </div>
</template>
<script>
  export default{
    mounted: function () {
      console.log("ManMonth prjId:" + this.prjid );
      this.loadPrjBudget(this.prjid);
    },
    methods: {
      updatePosNameExec(){
        if (!this.isNotNullORBlank(this.updatePosName)) {
          this.$message.warning(this.state == 'position' ? '职位名称不能为空!' : '职称名称不能为空!');
          return;
        }
        this.loading = true;
        var _this = this;
        this.putRequest(this.state=='position'?"/system/basic/position":"/system/basic/joblevel", {name: this.updatePosName, id: this.updatePosId,titleLevel:this.updateTitleLevel}).then(resp=> {
          _this.loading = false;
          if (resp && resp.status == 200) {
            this.dialogVisible = false;
            var data = resp.data;
            _this.$message({type: data.status, message: data.msg});
            _this.loadTableData();
          }
        })
      },
      deleteMany(){
        var _this = this;
        this.$confirm('删除' + this.multipleSelection.length + '条数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          var multipleSelection = _this.multipleSelection;
          var ids = '';
          multipleSelection.forEach(row=> {
            ids = ids + row.id + ',';
          })
          _this.doDelete(ids);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      addPosition(){
        if (!this.isNotNullORBlank(this.positionName)) {
          this.$message.warning(this.state == 'position' ? '职位名称不能为空!' : '职称名称不能为空!');
          return;
        }
        if (this.state == 'jobtitle') {
          if (!this.isNotNullORBlank(this.titleLevel)) {
            this.$message.warning('请选择职称级别!');
            return;
          }
        }
        var _this = this;
        this.loading = true;
        this.postRequest(this.state == 'position' ? "/system/basic/position" : "/system/basic/joblevel", {
          name: this.positionName,
          titleLevel: this.titleLevel
        }).then(resp=> {
          _this.loading = false;
          if (resp && resp.status == 200) {
            var data = resp.data;
            _this.$message({type: data.status, message: data.msg});
            _this.loadTableData();
            _this.positionName = '';
            _this.titleLevel = '';
          }
        });
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleEdit(index, row){
        this.updatePosName = row.name;
        this.updatePosId = row.id;
        this.updateTitleLevel=row.titleLevel;
        this.dialogVisible = true;
      },
      handleDelete(index, row){
        var _this = this;
        this.$confirm('删除[' + row.name + '], 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          _this.doDelete(row.id);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      doDelete(ids){
        var _this = this;
        _this.loading = true;
        var url = this.state == 'position' ? '/system/basic/position/' : '/system/basic/joblevel/';
        this.deleteRequest(url + ids).then(resp=> {
          _this.loading = false;
          if (resp && resp.status == 200) {
            _this.loadTableData();
          }
        })
      },


      loadPrjBudget(prjId){
        var _this = this;
        _this.curPrjId = prjId;
        _this.tableLoading = true;
        _this.getRequest("/project/budget/manmonth/"+ prjId).then( resp=> {
          _this.tableLoading = false;
          console.log( resp.status);

          if( resp && resp.status == 200 ){
 

            _this.sortPrjBudget( resp.data );
            _this.prjBudget = resp.data;
          }
          else
          {
            _this.curPrjId = -1;
          }
        })
      },


      sortPrjBudget( data )
      {
        if( data.amounts == null || data.amounts.length == 0 )
          return null;

          var budgets = new Array();
          var curYear = data.amounts[0].year;
          var idx = 0, k=0;

          console.log( curYear);
          budgets[idx] = new Object();

          budgets[idx].budgets = new Array();
          budgets[idx].year = curYear;

          for( var i=0; i<data.amounts.length; i++)
          {
             if(data.amounts[i].year != curYear )
             {
                k=0;
                idx++;
                curYear = data.amounts[i].year;

                budgets[idx] = new Object();

                budgets[idx].budgets = new Array();
                budgets[idx].year = curYear;
             }
              
              budgets[idx].budgets[k++] = data.amounts[i];
          }
          data.budgets = budgets;
      },


      loadTableData(){
        var _this = this;
        this.loading = true;
        this.getRequest(this.state == 'position' ? "/system/basic/positions" : "/system/basic/joblevels").then(resp=> {
          _this.loading = false;
          if (resp && resp.status == 200) {
            _this.posData = resp.data;
          }
        })
      }
    },

    data(){
      return {
        loading: false,
        multipleSelection: [],
        posData: [],

        prjBudget:{
          prjId:-1,
          prjName:'test',
          prjNo:'23-324',
          prjManCostAvg:0,
          contractAmount:0,
          curYearAmount:0,
          budgets:[
            {
              year:2017,
              budgets:[
                  {month:8, amount:0, manMonth:2, costAmount:0, realManMonth:0},
                  {month:9, amount:0, manMonth:3, costAmount:0, realManMonth:0},
                  {month:10, amount:0, manMonth:1, costAmount:0, realManMonth:0},
                  {month:11, amount:0, manMonth:4, costAmount:0, realManMonth:0},
                  {month:12, amount:0, manMonth:5, costAmount:0, realManMonth:0}
              ] 
            },
            {
              year:2018,
              budgets:[
                  {month:1, amount:0, manMonth:3, costAmount:0, realManMonth:0},
                  {month:2, amount:0, manMonth:4, costAmount:0, realManMonth:0},
                  {month:3, amount:0, manMonth:1, costAmount:0, realManMonth:0},
                  {month:4, amount:0, manMonth:2, costAmount:0, realManMonth:0},
                  {month:5, amount:0, manMonth:6, costAmount:0, realManMonth:0}

              ]
            }
          ]
        },
        
        budgetRules:{
          prjManCostAvg:[{required:true, message:'不能为空', trigger:'blur'}]
        }

      }
    },
    props: ['prjid']
  }
</script>



